import * as React from 'react';
import { useSearchParams } from 'react-router-dom';

import { Response } from '@miq/utiljs';
import { Loading } from '@miq/componentjs';

import StaffView from '../Views';

import { useLIBListRequest } from './utils';
import { TLIB } from './types';
import { StaffLink } from '../components';

export const LIBListFilterForm = ({ ...props }: {}) => {
  const [params, setParams] = useSearchParams();
  const [q, setQ] = React.useState<string>(params.get('q') || '');

  return (
    <form
      action="."
      method="GET"
      onSubmit={(e) => {
        e.preventDefault();

        if (!q) params.delete('q');
        else params.set('q', q);
        params.delete('page');

        setParams(params);
      }}
    ></form>
  );
};

export default function LIBListView() {
  const { res, loading } = useLIBListRequest();

  if (loading) return <Loading />;

  const r = new Response<TLIB>(res);
  if (!r.isSuccess) return null;

  return (
    <StaffView title="Links in bio">
      {r.items.map((item) => {
        return (
          <>
            <div>
              <StaffLink to={`${item.slug}/`}>{item.name}</StaffLink>
            </div>
          </>
        );
      })}
    </StaffView>
  );
}
